@import url(https://db.onlinewebfonts.com/c/63f874d192fb3892d88d5e26f942b5e2?family=DS-Digital);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
#label-container{display:flex;flex-direction:column;align-items:center;justify-content:flex-end}#label-container .label{margin-bottom:5px}#label-container .label-control{cursor:pointer;font-size:1.5em}#label-container .label-control:hover{opacity:0.5;-webkit-animation:enlarge;animation:enlarge;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes enlarge{100%{-webkit-transform:scale(1.3);transform:scale(1.3)}}@keyframes enlarge{100%{-webkit-transform:scale(1.3);transform:scale(1.3)}}

.orange-border{border-color:orange;-webkit-transition:border-color 1s ease-in-out;transition:border-color 1s ease-in-out}.green-border{border-color:green;-webkit-transition:border-color 1s ease-in-out;transition:border-color 1s ease-in-out}.red-border{border-color:red;-webkit-transition:border-color 1s ease-in-out;transition:border-color 1s ease-in-out}.orange-text{color:orange;-webkit-transition:color 1s ease-in-out;transition:color 1s ease-in-out}.green-text{color:green;-webkit-transition:color 1s ease-in-out;transition:color 1s ease-in-out}.red-text{color:red;-webkit-transition:color 1s ease-in-out;transition:color 1s ease-in-out}.blinker{-webkit-animation:blinker;animation:blinker;-webkit-animation-duration:0.6s;animation-duration:0.6s;-webkit-animation-timing-function:linear;animation-timing-function:linear;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes blinker{50%{opacity:0.4}}@keyframes blinker{50%{opacity:0.4}}

#timer-container{width:100%;display:flex;flex-direction:column}#timer-container #timer{margin:auto;width:100%}#timer-container #timer #timer-label{display:block;text-align:center;font-size:2em;border-width:2px;border-style:solid;border-radius:50px;width:35%;margin:auto}#timer-container #timer #timer-controls-container{display:flex;flex-direction:row;justify-content:space-evenly;align-items:center}#timer-container #timer #timer-controls-container #time-left{margin-left:0.2em;margin-right:0.1em;display:block;font-family:'DS-Digital';font-size:6em}.controls{cursor:pointer}.controls:hover{opacity:0.5;-webkit-animation:pulse;animation:pulse;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out}@-webkit-keyframes pulse{50%{-webkit-transform:scale(1.1);transform:scale(1.1)}}@keyframes pulse{50%{-webkit-transform:scale(1.1);transform:scale(1.1)}}#start-stop{font-size:2.3em}#reset{font-size:1.3em}

#labels{display:flex;flex-direction:row;width:100%;justify-content:space-evenly;margin-top:1.5em}#divider{width:90%;border-style:solid}#clock-container{margin:auto;margin-top:1em}@media (min-width: 320px){#clock-container{width:300px;height:300px}}@media (min-width: 600px){#clock-container{width:450px;height:450px}}

html,body{margin:0;height:100%;padding:0;overflow-x:hidden;font-family:'Source Sans Pro'}@media (min-width: 320px){html,body{font-size:0.9em}}@media (min-width: 600px){html,body{font-size:1.1em}}

